.timepicker-text-container {
    font-size: 4rem;
    font-weight: bold;
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    font-weight: 400;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.timepicker-span-hours,
.timepicker-span-minutes,
.timepicker-span-am-pm div {
    cursor: pointer
}

.timepicker-span-hours {
    margin-right: 3px
}

.timepicker-span-minutes {
    margin-left: 3px
}

.timepicker-display-am-pm {
    font-size: 1.3rem;
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    font-weight: 400
}

.timepicker-analog-display {
    flex: 2.5 1 auto
}

.timepicker-plate {
    background-color: #eee;
    border-radius: 50%;
    width: 270px;
    height: 270px;
    overflow: visible;
    position: relative;
    margin: auto;
    margin-top: 25px;
    margin-bottom: 5px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.timepicker-canvas,
.timepicker-dial {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0
}

.timepicker-minutes {
    visibility: hidden
}

.timepicker-tick {
    border-radius: 50%;
    color: rgba(0, 0, 0, 0.87);
    line-height: 40px;
    text-align: center;
    width: 40px;
    height: 40px;
    position: absolute;
    cursor: pointer;
    font-size: 15px
}

.timepicker-tick.active,
.timepicker-tick:hover {
    background-color: rgba(38, 166, 154, 0.25)
}

.timepicker-dial {
    transition: transform 350ms, opacity 350ms
}

.timepicker-dial-out {
    opacity: 0
}

.timepicker-dial-out.timepicker-hours {
    transform: scale(1.1, 1.1)
}

.timepicker-dial-out.timepicker-minutes {
    transform: scale(0.8, 0.8)
}

.timepicker-canvas {
    transition: opacity 175ms
}

.timepicker-canvas line {
    stroke: #26a69a;
    stroke-width: 4;
    stroke-linecap: round
}

.timepicker-canvas-out {
    opacity: 0.25
}

.timepicker-canvas-bearing {
    stroke: none;
    fill: #26a69a
}

.timepicker-canvas-bg {
    stroke: none;
    fill: #26a69a
}

.timepicker-footer {
    margin: 0 auto;
    padding: 5px 1rem;
    display: flex;
    justify-content: space-between
}

.timepicker-clear {
    color: #F44336
}

.timepicker-close {
    color: #26a69a
}

.timepicker-clear,
.timepicker-close {
    padding: 0 20px
}

@media only screen and (min-width: 601px) {
    .timepicker-modal {
        max-width: 600px
    }

    .timepicker-container.modals-content {
        flex-direction: row
    }

    .timepicker-text-container {
        top: 32%
    }

    .timepicker-display-am-pm {
        position: relative;
        right: auto;
        bottom: auto;
        text-align: center;
        margin-top: 1.2rem
    }
}
