@import "../../styles/lavenst.scss";

@keyframes openning {
    0% {
        transform: rotate(10deg);
    }

    7% {
        transform: rotate(-10deg);
    }

    14% {
        transform: rotate(10deg);
    }

    28% {
        transform: rotate(-10deg);
        margin-top: 96px;
    }

    42% {
        transform: rotate(0);
        margin-top: 150px;
        transform: scale(1);
    }

    50% {
        transform: scale(.9);
    }
    

    100% {
        margin-top: 150px;
        transform: scale(.9);
    }
}

@keyframes openningThu {
    0% {
        visibility: visible;
    }

    50% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(1);
        opacity: 1;
        visibility: visible;
    }
}

.thu_lixi_container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left:0;
    background-color: rgba(0,0,0,.55);
    backdrop-filter: blur(20px) saturate(280%);
    z-index: 9999999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.thu_lixi {
    width: 500px;
    height: auto;
    position: absolute;
    background-color: #fff8e1;
    border-radius: 13px;
    padding: 24px 48px;
    background-color: #fff8e1e6;
    backdrop-filter: blur(20px) saturate(180%);
    box-shadow: 0 4px 20px rgba(0,0,0,.12);
    z-index: 99999999;
    transform: scale(.9);
    opacity: 0;
    visibility: hidden;
    height:80%;
    max-height: 80%;
    overflow: auto;

    &::-webkit-scrollbar {
        display: none;
    }

    @include mobile {
        width:80%;
    }

    @include tablet {
        width: 80%;
    }

    &.openning {
        animation-delay: 1s!important;
        animation: 2s linear openningThu forwards;
    }

    & p {
        color: #3a3a3a;
        font-family: 'Dancing Script', cursive;
        font-size: 32px;
    }

    & span {
        color: #3a3a3a;
        font-family: 'Dancing Script', cursive;
        font-size: 32px;
        text-align:center;
        width: 100%;
        display: block;
    }
}

.lixi {
    margin-top: 96px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    @include mobile {
        margin-top: 64px;
    }

    @include tablet {
        margin-top: 64px;
    }

    &.openning {
        animation: 2s linear openning forwards;
    }

    & .open_btn {
        width: 96px;
        height: 48px;
        background: rgba(255,255,255,.75);
        position: absolute;
        border-radius: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 4px 10px rgba(0,0,0,.25);
        cursor: pointer;
        color: #333;
        font-weight: bold;
        border: 4px solid #fff;
        transition: all .25s ease;
        backdrop-filter: blur(20px) saturate(180%);
        z-index: 3;

        &:hover {
            transform: scale(1.1);
        }

        &:active {
            transform: scale(.9);
        }
    }

    & .image {
        border-radius: 24px;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        width: 400px;
        z-index: 2;
        &::after {
            content: "";
            display: block;
            padding-bottom: 150%;
        }

        @include mobile {
            width: 300px;
        }
    }

    & .image-shadow {
        position: absolute;
        width: 70%;
        height: 100%;
        overflow: hidden;
        display: flex;
        left: 50%;
        transform: translate(-50%, 0);
        bottom: -8px;
        z-index: 0;
        border-radius: 20px;
        filter: blur(10px);
        z-index: 1;

        & .image-shadow-image {
            margin-top: 0;
            object-fit: cover;
            width: 100%;
            height: 100%;
            opacity: .75;
            background-position: center center;
            background-size: cover;
        }
    }

    &.left, &.right {
        margin: 24px;

        & .image {    
            width: 350px!important;
        }
    }
}